.post-list-container {
  width: 100%;
}

.no-resources {
  text-align: center;
}

.spinner {
  display: flex;
  justify-content: center;
  margin-top: 100px;
  margin-left: 45%;
  margin-right: 45%;
  font-size: 2.5rem;
  color: silver;

  animation: spin infinite 1s linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.pagination-buttons-container {
  width: 200px;
  margin: 0 40%;
}

.prev-button {
  min-width: 70px;
  padding: 10px;
  margin: 0 10px;
  font-size: 1rem;
}

.next-button {
  min-width: 70px;
  padding: 10px;
  margin: 0 10px;
  font-size: 1rem;
}
